@import url(https://fonts.googleapis.com/css2?family=Outfit:wght@100..900&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: Outfit;
}

body {
  min-height: 100vh;
}

.App {
  width: 80%;
  margin: auto;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.navbar {
    padding: 20px 0px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-size: 3rem;
    font-weight: 800;
    color: #f69b2c;
    align-items: left;
}

.navbar-menu {
    display: flex;
    list-style: none;
    grid-gap: 20px;
    gap: 20px;
    color: black;
    font-size: 18px;
}

.navbar-right {
    display: flex;
    align-items: center;
    grid-gap: 40px;
    gap: 40px;
}

.navbar button {
    background: transparent;
    font-size: 16px;
    color: black;
    border: 1px solid #f69b2c;
    padding: 10px 30px;
    border-radius:  50px;
    cursor: pointer;
    transition: 0.3s;
}

.navbar button:hover {
    background-color: #fbd19e;
}

.navbar .active {
    padding-bottom: 2px;
    border-bottom: 2px solid black;
}

.navbar li {
    cursor: pointer;
}

.navbar-search-icon {
    position: relative;
}

.navbar-search-icon .dot {
    position: absolute;
    min-width: 10px;
    min-height: 10px;
    background-color: tomato;
    border-radius: 5px;
    top: -10px;
    right: -8px;
}

.header {
    height: 34vw;
    margin: 30px auto;
    background: url('/header_img.jpg');
    background-size: contain;
    position: relative;
}

.header-contents {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 1.5vw;
    gap: 1.5vw;
    max-width: 50%;
    bottom: 10%;
    left: 6vw;
    animation: fadeIn 3s;
}

.header-contents h2 {
    font-weight: 500;
    color: white;
    font-size: max(4.5vw, 22px);
}

.header-contents p {
    color: white;
    font-size: 1vw;
}

.header-contents button {
    border: none;
    color: tomato;
    font-weight: 500;
    padding: 1vw 2.3vw;
    background-color: white;
    font-size: max(1vw, 13px);
    border-radius: 50px;
}
